<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>

    <div class="content-container">
      <el-row>
        <el-col :span="10">
          <el-form
            :model="formData"
            ref="formData"
            label-width="100px"
            label-position="left"
          >
            <el-form-item label=":" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=""
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <!-- <el-table-column align="center" type="selection" width="100px"></el-table-column> -->
            <el-table-column
              align="center"
              label=""
              prop="customer_id"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="company_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="product_name"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label="SKU"
              prop="product_sku"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="come_time"
              width="130"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="out_time"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="storage_count"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="leave_count"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="save_days"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="out_count"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      times: 0, // 
      timer: null,
      loading: false, // false,true
      productReqUrl: "", // 
      productReqData: {}, // 
      locateReqUrl: "/api/query/getLocateCustomerId", // 
      locateReqData: {}, // 
      nameTipsArray: [], // 
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData: [], // 
      customer_info_list: [], // 
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {},
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
    // 
    formSearch() {
      this.getProduct();
    },
    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },
    // 
    getProduct() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getProductLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          this.queryResData = [];
          console.log(res);
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;
              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
              item.count = 0;
              item.out_time = utcToCst(item.out_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
              item.leave_count =
                Number(item.storage_count) - Number(item.out_count);
            }
            this.queryResData = res.data;
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    getAllProductByTime() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "/api/query/getProductLocateByTime",
        data:{
          customer_info:this.formData.customer_info,
        }
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;
              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
              item.count = 0;

              item.out_time = utcToCst(item.out_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
              ileaverage_count =
                Number(item.storage_count) - Number(item.out_count);
            }
            this.queryResData = res.data;
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // ,
    refresh() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "/api/query/getProductLocateByTime",
        data:{
          customer_info:this.formData.customer_info
        }
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          for (let item of res.data) {
            let come = Date.parse(new Date(item.come_time));
            let today = Date.parse(new Date(this.today_date));
            var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
            item.save_days = (isNaN(day)||day<0 )? 0 :day;
            item.come_time = utcToCst(item.come_time)
              .slice(0, 10)
              .replace(/|||||||/g, "");
            item.count = 0;

            item.out_time = utcToCst(item.out_time)
              .slice(0, 10)
              .replace(/|||||||/g, "");
            item.leave_count =
              Number(item.storage_count) - Number(item.out_count);
          }
          this.queryResData = res.data;
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getProductLocate",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        // id
        if (/[0-9a-z]/i.test(this.formData.customer_info)) {
          this.locateReqUrl = "/api/query/getLocateCustomerId";
          this.locateReqData = { customer_id: nV.customer_info };
        } else {
          this.locateReqUrl = "api/query/getLocateCompanyName";
          this.locateReqData = { company_name: nV.customer_info };
        }
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
  },
  computed: {
    computedQueryResData() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData.slice(size * (current - 1), size * current);
    },
  },
  mounted() {
    this.refresh();
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;
  },
  created() {
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/");
    }
  },
};
</script>

<style lang="less" scoped>
.productPage-addForm {
  margin-top: -80px;
  /deep/ .el-form-item__error {
    padding-left: 20px;
  }
}
.sa-container {
  display: inline-block;
  margin-left: -15px;
  min-width: 90px;
}
</style>
