<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>

    <div>
      <el-row  class="content-container">
        <el-col :span="10">
          <el-form
            :model="formData"
            ref="formData"
            label-width="100px"
            label-position="left"
          >
            <el-form-item label=":" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=""
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <el-table-column
              align="center"
              label=""
              prop="company_name"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="product_name"
              width="200px"
            ></el-table-column>
            <el-table-column
              align="center"
              label="(cm)"
              prop="length_width_height"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label="(lb)"
              prop="weight"
              width="150"
            ></el-table-column>
            <el-table-column
              align="center"
              label="(lb)"
              prop="real_weight"
              width="150"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="count"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="leave_count"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="come_time"
              width="110px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="save_days"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      customer_id: "",
      tableHeight: window.innerHeight * 0.65,
      times: 0, // 
      timer: null,
      loading: false, // false,true
      today_date: "", // 
      isDel: true, // 
      multipleSelection: [], // 
      solidSelection: [], // 
      pickerOption: {
        disabledDate: (time) => {
          return time.getTime() > Date.now(); /**/
        },
      },
      addDV: false, // 
      boxReqUrl: "", // 
      boxReqData: {}, // 
      locateReqUrl: "/api/query/getLocateCustomerId", // 
      locateReqData: {}, // 
      nameTipsArray: [], // 
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData: [], // 
      customer_info_list: [], // 
      louqueForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        length_width_height: "(cm)",
        weight: "(lb)",
        count: "",
        save_days: "",
        out_count: "",
        box_code: "",
        status: "",
      },
      addForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        length_width_height: "",
        weight: "",
        count: "",
        save_days: "",
        out_count: "",
        box_code: "",
        status: "",
      },
      rules: {
        customer_info: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {
      if (val.length == 0) {
        this.isDel = true;
      } else {
        this.isDel = false;
      }
      this.multipleSelection = val;
    },
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
    // 
    formSearch() {
      this.getBox();
    },
    // 
    refreshFormSearch() {
      this.refreshGetBox();
    },
    // 
    getBox() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getSingleBoxLocateByName",
        data: {
          customer_info: this.formData.customer_info,
          customer_id:this.customer_id
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;

              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
                  item.leave_count = Number(item.count) 
                 item.count = Number(item.count)+Number(item.out_count)
            }
            this.queryResData = res.data;
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    refreshGetBox() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getBoxLocateByName",
        data: {
          customer_info: this.customer_id,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          console.log(res);
          if (res.data.length != 0) {
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;

              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
                item.leave_count = Number(item.count) 
                 item.count = Number(item.count)+Number(item.out_count)
              
            }
            this.queryResData = res.data;
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    deleteSubmit() {
      this.$confirm("?")
        .then(() => {
          this.solidSelection = this.multipleSelection;
          while (this.solidSelection.length != 0) {
            this.deleteBox();
            this.solidSelection.shift();
          }
          this.$message.success("!");
        })
        .catch((err) => {
          this.$message.warning("");
        });
    },
    // 
    deleteBox() {
      this.$http({
        method: "post",
        url: "api/delete/deleteBox",
        data: {
          customer_id: this.solidSelection[0].customer_id,
          box_code: this.solidSelection[0].box_code,
        },
      })
        .then((res) => {
          this.refreshFormSearch();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },
    // 
    getData() {
      console.log(this.formData.customer_info)
      console.log(this.customer_id)
      this.$http({
        method: "post",
        url: "api/query/getSingleBoxLocate",
        data: {
          customer_info: this.formData.customer_info,
          customer_id: this.customer_id,
        },
      })
        .then((res) => {
          console.log(res)
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
    // ()
    addOpen() {
      if (this.formData.customer_info != "") {
        this.addForm.customer_id = this.formData.customer_info;
      }
      this.addDV = true;
    },
    // 
    addSubmit() {
      // 
      for (let key in this.addForm) {
        if (key == "box_code") {
          continue;
        }
        if (this.addForm[key] == "") {
          this.$message.error(`--${this.louqueForm[key]}--`);
          return;
        }
      }
      this.addForm.box_code =Math.random().toString(16).slice(2,15);
      this.$http({
        method: "post",
        url: "api/insert/insertBox",
        data: this.addForm,
      })
        .then((res) => {
          this.$message.success("!");
          this.refreshGetBox();
          this.addDV = false;
        })
        .catch((err) => {});
    },
    // id
    checkCustomerId() {
      this.$http({
        method: "post",
        url: "api/query/getCustomerId",
        data: {
          customer_id: this.addForm.customer_id,
        },
      })
        .then((res) => {
          if (res.data.length == 0) {
            this.$message.warning(
              `${this.addForm.customer_id}`
            );
          } else {
            this.addSubmit();
          }
        })
        .catch((err) => {});
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        // id
        if (/[0-9a-z]/i.test(this.formData.customer_info)) {
          this.locateReqUrl = "/api/query/getLocateCustomerId";
          this.locateReqData = { customer_id: nV.customer_info };
        } else {
          this.locateReqUrl = "api/query/getLocateCompanyName";
          this.locateReqData = { company_name: nV.customer_info };
        }
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
    addForm: {
      handler: function(nV, oV) {
        // nV.save_days = nV.come_time;

        // 
        if (this.addForm.come_time != "") {
          var a1 = Date.parse(new Date(nV.come_time));
          var a2 = Date.parse(new Date(this.today_date));
          var day = parseInt((a2 - a1) / (1000 * 60 * 60 * 24)); //
          nV.save_days = (isNaN(day)||day<0 )? 0 :day + 1;
        }

        // 
        if (this.addForm.length_width_height.length > 4) {
          let lwhArr = this.addForm.length_width_height.split("*");
          if (lwhArr.length == 3) {
            let result = Math.floor(
              (lwhArr[0] * lwhArr[1] * lwhArr[2]) / 6000 / 0.452
            );
            this.addForm.weight = result;
          }
        }
      },
      deep: true,
    },
  },
  computed: {
    computedQueryResData() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData.slice(size * (current - 1), size * current);
    },
    totalLeaveCount() {
      let sum = 0;
      for (let item of this.queryResData) {
        sum += item.leave_count;
      }
      return sum;
    },
  },
  mounted() {
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;

    this.refreshFormSearch();
  },
  created() {
    this.customer_id = sessionStorage.getItem("userName");
    this.level = sessionStorage.getItem("userLevel");
  },
};
</script>

<style lang="less" scoped>
.productPage-addForm {
  margin-top: -95px;
  /deep/ .el-form-item__error {
    padding-left: 20px;
  }
}
</style>
