<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>

    <div class="content-container">
      <el-row>
        <el-col :span="10">
          <el-form :model="formData" ref="formData" label-width="100px" label-position="left">
            <el-form-item label=":" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=""
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>
      </el-row>
      <el-row style="padding-top:5px">
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <el-table-column align="center" type="selection" width="100px"></el-table-column>
            <el-table-column align="center" label="" prop="customer_id"></el-table-column>
            <el-table-column align="center" label="" prop="company_name"></el-table-column>
            <el-table-column align="center" label="" prop="product_name" width="270px"></el-table-column>
            <el-table-column align="center" label="sku" prop="product_sku" ></el-table-column>
            <el-table-column align="center" label="" prop="out_count"></el-table-column>
            <el-table-column align="center" label="">
              <template slot-scope="scope">
                <el-link type="primary" style="font-size:16px" @click="showAddress(scope)"></el-link>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb padding_lr">
        <el-col :span="2">
          <el-button type="danger" :disabled="isDel" @click="deleteSubmit"></el-button>
        </el-col>
      </el-row>
    </div>

    <el-dialog
      :close-on-click-modal="false"
      title="--"
      class="cus-dialog-style"
      :visible.sync="addDV"
      width="55%"
      :before-close="handleClose"
    >
      <el-form v-model="addressForm" ref="addressForm" label-width="340px">
        <el-row type="flex" justify="center" style="margin-bottom:10px;margin-top:25px">
          <el-form-item required label=" Enter customer address:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.customer_address"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item required label=" Full name:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.full_name"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item required label="1 Street address1:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.street_address1"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item required label="2 Street address2:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.street_address2"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item required label=" City:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.city"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item required label="/ State/Province:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.province"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item required label=" Zip / Postal code:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.zip"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:25px;margin-top:25px">
          <el-form-item label="() Phone number:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.phone_number"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row type="flex" justify="center" style="margin-bottom:10px;margin-top:15px">
          <el-form-item label="() Email address:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addressForm.email_address"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" plain @click="addDV = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      times: 0, // 
      timer: null,
      loading: false, // false,true
      isDel: true, // 
      multipleSelection: [], // 
      solidSelection: [], // 
      outRecordReqUrl: "", // 
      outRecordReqData: {}, // 
      locateReqUrl: "/api/query/getLocateCustomerId", // 
      locateReqData: {}, // 
      nameTipsArray: [], // 
      addDV: false,
      addressForm: {
        // 
        customer_address: "", //
        full_name: "", // 
        street_address1: "", // 1
        street_address2: "", // 2
        city: "", // 
        province: "", // 
        zip: "", // 
        phone_number: "", // 
        email_address: "", // 
        product_code: "", // 
        box_code: "", // 
        record_code: "", // 
      },
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData: [], // 
      customer_info_list: [], // 
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {
      console.log(val);
      if (val.length == 0) {
        this.isDel = true;
      } else {
        this.isDel = false;
      }
      this.multipleSelection = val;
    },
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
    // 
    formSearch() {
      this.getOutRecord();
    },
    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },
    // 
    getOutRecord() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getOutRecordLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          if (res.data.length != 0) {
            this.queryResData = [];
            this.$message.success("");
            for (let item of res.data) {
              console.log(item.out_source);
              if (item.status == "true" || item.out_source != "") {
                continue;
              } else {
                item.out_time = utcToCst(item.out_time)
                  .slice(0, 10)
                  .replace(/|||||||/g, "");
                this.queryResData.push(item);
              }
            }
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    getAllOutRecord() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "/api/query/getAllOutRecord",
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          this.queryResData = [];
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              if (item.status == "true" || item.out_source != "") {
                continue;
              } else {
                item.out_time = utcToCst(item.out_time)
                  .slice(0, 10)
                  .replace(/|||||||/g, "");
                this.queryResData.push(item);
              }
            }
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    //   ,
    refreshGetAllOutRecord() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "/api/query/getAllOutRecord",
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          console.log(res);
          this.queryResData = [];
          for (let item of res.data) {
            if (item.status == "true" || item.out_source != "") {
              // 
              continue;
            } else {
              item.out_time = utcToCst(item.out_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
              this.queryResData.push(item);
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    deleteSubmit() {
      this.$confirm("?")
        .then(() => {
          this.solidSelection = this.multipleSelection;
          while (this.solidSelection.length != 0) {
            this.withdrawOutRecord();

            // this.deleteOutRecord();
            // this.deleteAddress(); //
            this.solidSelection.shift();
          }
          this.$message.success("!");
        })
        .catch((err) => {
          this.$message.warning("");
        });
    },
    //   
    deleteOutRecord() {
      this.$http({
        method: "post",
        url: "api/delete/deleteOutRecord",
        data: {
          customer_id: this.solidSelection[0].customer_id,
          product_name: this.solidSelection[0].product_name,
          product_sku: this.solidSelection[0].product_sku,
          out_time: this.solidSelection[0].out_time,
          out_count: this.solidSelection[0].out_count,
          record_code: this.solidSelection[0].record_code,
        },
      })
        .then((res) => {
          this.refreshGetAllOutRecord();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    //   
    withdrawOutRecord() {
      let url = "api/update/updateOutRecordWithBox";
      let data = {
        status: "true",
        customer_id: this.solidSelection[0].customer_id,
        product_name: this.solidSelection[0].product_name,
        product_sku: this.solidSelection[0].product_sku,
        out_count: this.solidSelection[0].out_count,
        out_time: this.solidSelection[0].out_time,
        record_code: this.solidSelection[0].record_code,
      };
      if (this.solidSelection[0].product_sku != "---") {
        url = "api/update/updateOutRecordWithProduct";
      }

      console.log(this.solidSelection[0]);
      console.log(url);
      console.log(data);

      this.$http({
        method: "post",
        url: url,
        data: data,
      })
        .then((res) => {
          this.refreshGetAllOutRecord();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    showAddress(scope) {
      console.log(scope);

      this.debounce(this.getAddress(scope), 1000);
    },

    //
    getAddress(scope) {
      this.$http({
        method: "post",
        url: "api/query/getAddress",
        data: {
          record_code: scope.row.record_code,
        },
      })
        .then((res) => {
          console.log(res);
          this.addressForm = res.data[0];

          setTimeout(() => {
            this.addDV = true;
          }, 200);
        })
        .catch((err) => {
          console.log(err);
        });
    },

    handleClose() {
      this.addressForm = {};
      this.addDV = false;
    },
    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getOutRecordLocate",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              if (item.out_source == "") {
                continue;
              }
              // item
              for (let prop in item) {
                if (String(item[prop]).indexOf(this.formData.customer_info) != -1) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        // id
        if (/[0-9a-z]/i.test(this.formData.customer_info)) {
          this.locateReqUrl = "/api/query/getLocateCustomerId";
          this.locateReqData = { customer_id: nV.customer_info };
        } else {
          this.locateReqUrl = "api/query/getLocateCompanyName";
          this.locateReqData = { company_name: nV.customer_info };
        }
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
  },
  computed: {
    computedQueryResData() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData.slice(size * (current - 1), size * current);
    },
  },
  mounted() {
    this.refreshGetAllOutRecord();
    var today = new Date();
    today.setTime(today.getTime());
    var today_date = today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate();
    this.today_date = today_date;
  },
  created() {
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/");
    }
  },
};
</script>

<style lang="less" scoped>
.productPage-addressForm {
  margin-top: -80px;
  /deep/ .el-form-item__error {
    padding-left: 20px;
  }
}
.sa-container {
  display: inline-block;
  margin-left: -15px;
  min-width: 90px;
}
.cus-dialog-style {
  /deep/ .el-dialog {
    margin-top: 1vh !important;
  }
  .el-input {
    width: 300px !important;
  }
}
</style>
