<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>
    <div class="content-container">
      <el-row>
        <el-col style="width:40%">
          <el-form
            :model="formData"
            ref="formData"
            :rules="rules"
            label-width="120px"
            label-position="left"
          >
            <el-form-item label="" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=","
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>
      </el-row>

      <el-row style="padding-top:5px;">
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="queryResData_trading_pagination"
          >
            <el-table-column
              align="center"
              type="selection"
              width="100px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="customer_id"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="company_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="trading_date"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="trading_content"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="trading_amount"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="status"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[4, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData_trading.length"
          >
          </el-pagination>
        </el-col>
      </el-row>

      <el-row type="flex" class="padding_tb padding_lr">
        <el-col :span="2">
          <el-button class="add-btn" @click="addOpen"></el-button>
        </el-col>
        <el-col :span="18">
          <el-button type="warning" :disabled="isModify" @click="modifyOpen"
            ></el-button
          >
        </el-col>
        <el-col :span="2">
          <el-button type="danger" :disabled="isDel" @click="deleteSubmit"
            ></el-button
          >
        </el-col>
        <el-col :span="2">
          <el-button type="info" plain @click="toggleSelection()"
            ></el-button
          >
        </el-col>
      </el-row>
    </div>

    <el-dialog
      :close-on-click-modal="false"
      title="--"
      :visible.sync="addDV"
      width="35%"
      class="productPage-addForm"
      :before-close="handleClose"
    >
      <el-form
        :model="addForm"
        ref="addForm"
        :rules="addRules"
        label-width="150px"
      >
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="customer_id" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-autocomplete
                style="width:100%"
                v-model="addForm.customer_id"
                :fetch-suggestions="dialogQuerySearch"
                clearable
                placeholder=""
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="trading_date" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-date-picker
                :picker-options="pickerOption"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd-HH-ss"
                v-model="addForm.trading_date"
              ></el-date-picker>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="trading_content" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.trading_content"></el-input>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item
            prop="trading_amount"
            label="/:"
            labelWidth="160px"
          >
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.trading_amount"></el-input>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item prop="status" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-select v-model="addForm.status">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="checkCustomerId()"> </el-button>
        <el-button type="primary" plain @click="addDV = false"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      :close-on-click-modal="false"
      title="--"
      :visible.sync="modifyDV"
      width="35%"
      class="productPage-addForm"
      :before-close="handleCloseModify"
    >
      <el-form
        :model="modifyForm"
        ref="modifyForm"
        :rules="modifyRules"
        label-width="150px"
      >
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="customer_id" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-autocomplete
                style="width:100%"
                v-model="modifyForm.customer_id"
                :fetch-suggestions="dialogQuerySearch"
                clearable
                placeholder=""
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="trading_date" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-date-picker
                :picker-options="pickerOption"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd-HH-ss"
                v-model="modifyForm.trading_date"
              ></el-date-picker>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="trading_content" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="modifyForm.trading_content"></el-input>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item
            prop="trading_amount"
            label="/:"
            labelWidth="160px"
          >
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="modifyForm.trading_amount"></el-input>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item prop="status" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-select v-model="modifyForm.status">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="modifySubmit()"> </el-button>
        <el-button type="primary" plain @click="modifyDV = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      times: 0, // 
      timer: null,
      loading: false, // false,true
      isDel: true,
      isModify: true,
      multipleSelection: [], // 
      solidSelection: [], // 
      today_date: "", // 
      pickerOption: {
        disabledDate: (time) => {
          return time.getTime() > Date.now(); /**/
        },
      },
      addDV: false, // 
      modifyDV: false, // 
      tradingReqUrl: "", // 
      tradingReqData: {}, // 
      leaveAmountReqUrl: "", // 
      leaveAmountReqData: {}, // 
      rechargeReqUrl: "", // 
      rechargeReqData: {}, // 
      locateReqUrl: "/api/query/getLocateCustomerId", // 
      locateReqData: {}, // 
      nameTipsArray: [], // 
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData_leaveAmount: [], // 
      queryResData_recharge: [], // 
      queryResData_trading: [], // 
      customer_info_list: [], // 
      customer_id_list: [], //  
      addNameTipsArray: [], //  
      addForm: {
        customer_id: "",
        trading_date: "",
        trading_content: "",
        trading_amount: "",
        status: "",
        trading_code: "",
      },
      modifyForm: {
        customer_id: "",
        trading_date: "",
        trading_content: "",
        trading_amount: "",
        status: "",
        trading_code: "",
      },
      loutianForm: {
        // 
        customer_id: "",
        trading_date: "",
        trading_content: "",
        trading_amount: "/",
        status: "",
      },
      rules: {
        customer_info: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      addRules: {
        customer_id: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        trading_date: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        trading_content: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        trading_amount: [
          {
            message: "/",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        status: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      modifyRules: {
        customer_id: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        trading_date: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        trading_content: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        trading_amount: [
          {
            message: "/",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        status: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleCloseModify() {
      this.modifyDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {
      if (val.length == 0) {
        this.isDel = true;
        this.isModify = true;
      } else if (val.length == 1) {
        this.isModify = false;
        this.isDel = false;
      } else {
        this.isModify = true;
        this.isDel = false;
      }
      this.multipleSelection = val;
    },
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
    // 
    formSearch() {
      this.getTrading();
    },
    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },

    //  
    dialogQuerySearch(queruString, cb) {
      if (this.addForm.customer_id != "") {
        var addNameTipsArray = this.addNameTipsArray;
        cb(addNameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },

    // 
    getTrading() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getTradingByName",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = day;
              item.trading_date = utcToCst(item.trading_date)
                .slice(0, 10)
                .replace(/|||||||/g, "");
            }

            this.queryResData_trading = res.data;
          } else {
            this.queryResData_trading = [];
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    refreshFormSearch() {
      this.refreshGetTrading();
    },
    // 
    refreshGetTrading() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getTradingByName",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = day;
              item.trading_date = utcToCst(item.trading_date)
                .slice(0, 10)
                .replace(/|||||||/g, "");
            }

            this.queryResData_trading = res.data;
          } else {
            this.queryResData_trading = [];
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    insertTrading() {
      this.$http({
        method: "post",
        url: "api/insert/insertTrading",
        data: this.addForm,
      })
        .then((res) => {
          this.$message.success("!");
          this.refreshGetTrading();
          this.addDV = false;
        })
        .catch((err) => {});
    },
    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getTradingLocate",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    getDialogData() {
      let prop;
      if (this.modifyDV == true) {
        prop = this.modifyForm.customer_id;
      } else if (this.addDV == true) {
        prop = this.addForm.customer_id;
      }
      this.$http({
        method: "post",
        url: "api/query/getCustomerDialogLocate",
        data: {
          customer_info: prop,
        },
      })
        .then((res) => {
          this.customer_info_list = [];
          this.customer_id_list = res.data;
          if (this.addForm.customer_id != "") {
            this.addNameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_id_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.addForm.customer_id) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.addNameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
    // 
    addSubmit() {
      // 4
      this.addForm.trading_code = Math.random().toString(16).slice(2,15);
      // 
      for (let key in this.addForm) {
        if (this.addForm[key] == "") {
          this.$message.error(`:${this.loutianForm[key]}`);
          return;
        }
      }

      this.insertTrading();
    },
    // id
    checkCustomerId() {
      this.$http({
        method: "post",
        url: "api/query/getCustomerId",
        data: {
          customer_id: this.addForm.customer_id,
        },
      })
        .then((res) => {
          if (res.data.length == 0) {
            this.$message.warning(
              `${this.addForm.customer_id}`
            );
          } else {
            this.addSubmit();
          }
        })
        .catch((err) => {});
    },
    // 
    addOpen() {
      if (this.queryResData_leaveAmount.length != 0) {
        this.addForm.customer_id = this.queryResData_trading[0].customer_id;
      }
      this.addDV = true;
    },
    // 
    modifyOpen() {
      this.modifyForm.customer_id = this.queryResData_trading[0].customer_id;

      this.modifyForm.trading_code = this.multipleSelection[0].trading_code;
      this.modifyForm.trading_amount = this.multipleSelection[0].trading_amount;
      this.modifyForm.trading_content = this.multipleSelection[0].trading_content;
      this.modifyForm.trading_date = this.multipleSelection[0].trading_date;
      this.modifyForm.status = this.multipleSelection[0].status;
      this.modifyDV = true;
    },
    // 
    modifySubmit() {
      // 
      for (let key in this.modifyForm) {
        if (this.modifyForm[key] == "") {
          this.$message.error(`:${this.loutianForm[key]}`);
          return;
        }
      }
      this.modifyTrading();
    },

    // 
    modifyTrading() {
      this.$http({
        method: "post",
        url: "api/update/updateTrading",
        data: this.modifyForm,
      })
        .then((res) => {
          this.$message.success("");
          this.modifyDV = false;
          this.refreshGetTrading();
        })
        .catch((err) => {});
    },
    // 
    deleteTrading(arr) {
      let data = {};
      data.trading_code = this.solidSelection[0].trading_code;
      data.customer_id = this.solidSelection[0].customer_id;
      this.$http({
        method: "post",
        url: "api/delete/deleteTrading",
        data: data,
      })
        .then((res) => {
          this.refreshGetTrading();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    deleteSubmit() {
      if (this.multipleSelection.length != 0) {
        this.solidSelection = this.multipleSelection;
      }
      while (this.solidSelection.length != 0) {
        this.deleteTrading();
        this.solidSelection.shift();
      }
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
    addForm: {
      handler: function(nV, oV) {
        this.debounce(this.getDialogData, 1000);
      },
      deep: true,
    },
    modifyForm: {
      handler: function(nV, oV) {
        this.debounce(this.getDialogData, 1000);
      },
      deep: true,
    },
  },
  computed: {
    queryResData_trading_pagination() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData_trading.slice(
        size * (current - 1),
        size * current
      );
    },
    queryResData_recharge_pagination() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData_recharge.slice(
        size * (current - 1),
        size * current
      );
    },
  },
  mounted() {
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;

    this.refreshFormSearch();
  },
  created() {
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/");
    }
  },
};
</script>

