<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>

    <div class="content-container">
      <el-row>
        <el-col style="width:43%">
          <el-form
            :model="formData"
            ref="formData"
            label-width="120px"
            label-position="left"
          >
            <el-form-item label="" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=","
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button class="add-btn" @click="addOpen"></el-button>
        </el-col>
      </el-row>
      <el-row style="padding-top:10px">
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <el-table-column
              align="center"
              type="selection"
              width="100px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="customer_id"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="company_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="come_time"
              width="130px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="product_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label="SKU"
              prop="product_sku"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="storage_count"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="save_days"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb padding_lr">
        <el-col :span="2">
          <el-button type="danger" :disabled="isDel" @click="deleteSubmit"
            ></el-button
          >
        </el-col>
      </el-row>
    </div>

    <el-dialog
      :close-on-click-modal="false"
      title="--"
      :visible.sync="addDV"
      width="35%"
      class="productPage-addForm"
      :before-close="handleClose"
    >
      <el-form
        :model="addForm"
        ref="addForm"
        :rules="addRules"
        label-width="120px"
      >
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="customer_id" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-autocomplete
                style="width:100%"
                v-model="addForm.customer_id"
                :fetch-suggestions="dialogQuerySearch"
                clearable
                placeholder=""
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="come_time" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-date-picker
                :picker-options="pickerOption"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
                v-model="addForm.come_time"
              ></el-date-picker>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="product_name" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-autocomplete
                style="width:100%"
                v-model="addForm.product_name"
                :fetch-suggestions="dialogQuerySearch"
                clearable
                placeholder=""
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item prop="product_sku" label="SKU:">
            <el-col :span="24" style="padding-left:20px">
              <el-autocomplete
                style="width:100%"
                v-model="addForm.product_sku"
                :fetch-suggestions="dialogQuerySearch"
                clearable
                placeholder=""
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item prop="storage_count" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.storage_count"></el-input>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item prop="save_days" label=":">
            <el-col :span="24" style="padding-left:20px">
              <el-input disabled v-model="addForm.save_days"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="checkCustomerId()"> </el-button>
        <el-button type="primary" plain @click="addDV = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      times: 0, // 
      timer: null,
      loading: false, // false,true
      addCount: 0,
      today_date: "", // 
      pickerOption: {
        disabledDate: (time) => {
          return time.getTime() > Date.now(); /**/
        },
      },
      isDel: true, // 
      addDV: false, // 
      productReqUrl: "", // 
      productReqData: {}, // 
      nameTipsArray: [], // 
      customer_id_list: [], //  
      addNameTipsArray: [], //  
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData: [], // 
      customer_info_list: [], // 
      addForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        product_sku: "",
        storage_count: "",
        save_days: "",
        out_count: "0",
        record_code: "",
        status: "",
        product_code: "",
        out_time: "1970-01-01",
      },
      updateForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        product_sku: "",
        storage_count: "",
        save_days: "",
        out_count: "0",
        status: "",
        product_code: "",
        record_code: "",
      },
      louqueForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        product_sku: "SKU",
        storage_count: "",
        save_days: "",
        out_count: "",
        status: "",
        record_code: "",
      },
      rules: {
        customer_info: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      addRules: {
        customer_id: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        come_time: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        product_name: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        product_sku: [
          {
            message: "SKU",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        storage_count: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        out_count: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        status: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },
      multipleSelection: [], // 
      solidSelection: [], // 
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {
      if (val.length == 0) {
        this.isDel = true;
      } else {
        this.isDel = false;
      }
      this.multipleSelection = val;
    },
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
    // 
    formSearch() {
      this.loading = true;

      this.$http({
        method: "post",
        url: "api/query/getProductLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          this.queryResData = [];
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;

              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
            }
            this.queryResData = res.data;
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    refreshFormSearch() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getProductLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          this.queryResData = [];
          if (res.data.length != 0) {
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;
              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
            }
            console.log(res.data);
            this.queryResData = res.data;
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    //  
    dialogQuerySearch(queruString, cb) {
      if (this.addForm.customer_id != "") {
        var addNameTipsArray = this.addNameTipsArray;
        cb(addNameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },

    // 
    getDialogData() {
      let payload;
      if (
        this.addDV == true &&
        this.addForm.product_name == "" &&
        this.addForm.product_sku == ""
      ) {
        payload = this.addForm.customer_id;
      } else if (
        this.addDV == true &&
        this.addForm.customer_id != "" &&
        this.addForm.product_sku == ""
      ) {
        payload = this.addForm.product_name;
      } else if (
        this.addDV == true &&
        this.addForm.customer_id != "" &&
        this.addForm.product_name != ""
      ) {
        payload = this.addForm.product_sku;
      }
      console.log(payload);
      this.$http({
        method: "post",
        url: "api/query/getProductDialogLocate",
        data: {
          customer_info: payload,
        },
      })
        .then((res) => {
          console.log(res);
          this.customer_id_list = [];
          this.customer_id_list = res.data;
          if (this.addForm.customer_id != "") {
            this.addNameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_id_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(payload) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.addNameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },
    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getProductLocate",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          console.log(res);
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                console.log(item[prop]);
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
    // 
    deleteSubmit() {
      this.$confirm("?")
        .then(() => {
          this.solidSelection = this.multipleSelection;
          while (this.solidSelection.length != 0) {
            this.deleteProduct();
            this.solidSelection.shift();
          }
          this.$message.success("!");
        })
        .catch((err) => {
          this.$message.warning("");
        });
    },
    // 
    deleteProduct() {
      this.$http({
        method: "post",
        url: "api/delete/deleteProduct",
        data: {
          customer_id: this.solidSelection[0].customer_id,
          product_code: this.solidSelection[0].product_code,
        },
      })
        .then((res) => {
          this.refreshFormSearch();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // ()
    addOpen() {
      if (this.formData.customer_info != "") {
        this.addForm.customer_id = this.formData.customer_info;
      }
      this.addDV = true;
    },
    // 
    addSubmit() {
      /**
       *  
       *  sku
       * */
      let flag = false;
      console.log(this.addForm);
      // 
      for (let key in this.addForm) {
        if (
          key == "product_code" ||
          key == "out_time" ||
          key == "record_code"
        ) {
          continue;
        }
        if (this.addForm[key] == "" || this.addForm[key] == undefined || this.addForm[key] == null) {
          this.$message.error(` ${this.louqueForm[key]} `);
          return;
        }
      }

      console.log(this.addForm.product_name);

      this.$http({
        method: "post",
        url: "api/query/getSameSku",
        data: {
          customer_id: this.addForm.customer_id,
          product_name: this.addForm.product_name,
          product_sku: this.addForm.product_sku,
        },
      })
        .then((res) => {
          console.log(res);

          if (res.data.length == 0) {
            flag = true;
            console.log(res);
          } else {
            flag = false;
            console.log("0");
          }

          if (flag) {
            this.addForm.product_code =Math.random().toString(16).slice(2,15);
            this.$http({
              method: "post",
              url: "api/insert/insertProduct",
              data: this.addForm,
            })
              .then((res) => {
                this.$message.success("!");
                // this.addDV = false;
                console.log(res);
                this.refreshFormSearch();
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            this.updateForm = this.addForm;
            this.updateForm.product_code = res.data[0].product_code;
            this.updateForm.storage_count = String(
              Number(res.data[0].storage_count) +
                Number(this.addForm.storage_count)
            );
            this.updateForm.out_count = String(Number(res.data[0].out_count));
            console.log(this.updateForm);

            this.$http({
              method: "post",
              url: "api/update/updateProduct",
              data: this.updateForm,
            })
              .then((res) => {
                this.$message.success("");
                this.addDV = false;
                this.refreshFormSearch();
                console.log(this.updateForm);
                console.log(res);
              })
              .catch((err) => {
                console.log(err);
              });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // id
    checkCustomerId() {
      this.$http({
        method: "post",
        url: "api/query/getCustomerId",
        data: {
          customer_id: this.addForm.customer_id,
        },
      })
        .then((res) => {
          if (res.data.length == 0) {
            this.$message.warning(
              `${this.addForm.customer_id}`
            );
          } else {
            this.addSubmit();
          }
        })
        .catch((err) => {});
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
    addForm: {
      handler: function(nV, oV) {
        // nV.save_days = nV.come_time;
        if (this.addForm.come_time != "") {
          var a1 = Date.parse(new Date(nV.come_time));
          var a2 = Date.parse(new Date(this.today_date));
          var day = parseInt((a2 - a1) / (1000 * 60 * 60 * 24)); //
          nV.save_days = (isNaN(day)||day<0 )? 0 :day + 1;
        }
        this.debounce(this.getDialogData, 1000);
      },
      deep: true,
    },
  },
  computed: {
    computedQueryResData() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData.slice(size * (current - 1), size * current);
    },
  },
  mounted() {
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;

    this.refreshFormSearch();
  },
  created() {
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/");
    }
  },
};
</script>

<style lang="less" scoped>
.productPage-addForm {
  margin-top: -80px;
  /deep/ .el-form-item__error {
    padding-left: 20px;
  }
}
</style>
