<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>

    <div class="content-container">
      <el-row>
        <el-col style="width:auto">
          <span class="customer_id_style">:{{ customer_id }}</span>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="7">
          <el-form
            :model="formData"
            ref="formData"
            label-width="0"
            label-position="left"
          >
            <el-form-item prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                @change="listenShowFill"
                id="formSearch"
                placeholder=""
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>

        <el-col :span="3" style="margin-left:1%">
          <el-button
            :class="[isNoClick == true ? 'disabled-btn' : 'add-btn']"
            :disabled="isNoClick"
            @click="addOpen"
           	:style="{'display':isShowFill}"
            >(,)</el-button
          >
        </el-col>
      </el-row>

      <el-row>
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <el-table-column
              align="center"
              type="selection"
              width="100px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="product_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label="sku"
              prop="product_sku"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="storage_count"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="leave_count"
            ></el-table-column>
            <el-table-column align="center" label="">
              <template slot-scope="scope">
                <div class="sa-container">
                  <el-input
                    placeholder=""
                    v-model="scope.row.count"
                  ></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="come_time"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="save_days"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>

    <el-dialog
      :close-on-click-modal="false"
      title="--"
      class="cus-dialog-style"
      :visible.sync="addDV"
      width="55%"
      :before-close="handleClose"
    >
      <el-form v-model="addForm" ref="addForm" label-width="340px">
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:25px"
        >
          <el-form-item required label=" Enter customer address:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.customer_address"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item required label=" Full name:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.full_name"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item required label="1 Street address1:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.street_address1"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item required label="2 Street address2:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.street_address2"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item required label=" City:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.city"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item required label="/ State/Province:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.province"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item required label=" Zip / Postal code:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.zip"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:25px;margin-top:25px"
        >
          <el-form-item label="() Phone number:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.phone_number"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom:10px;margin-top:15px"
        >
          <el-form-item label="() Email address:">
            <el-col :span="24" style="padding-left:20px">
              <el-input v-model="addForm.email_address"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addToSubmit"> </el-button>
        <el-button type="primary" plain @click="addDV = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      isShowFill:'none',// 
      tableHeight: window.innerHeight * 0.65,
      customer_id: "", // 
      level: "", // 
      isNoClick: true, // 
      times: 0, // 
      timer: null,
      loading: false, // false,true
      today_date: "", // 
      pickerOption: {
        disabledDate: (time) => {
          return time.getTime() > Date.now(); /**/
        },
      },
      addDV: false, // 
      addForm: {
        // 
        customer_address: "", //
        full_name: "", // 
        street_address1: "", // 1
        street_address2: "", // 2
        city: "", // 
        province: "", // 
        zip: "", // 
        phone_number: "", // 
        email_address: "", // 
        product_code: "", // 
        box_code: "", // 
        record_code: "", // 
      },
      productReqUrl: "", // 
      productReqData: {}, // 
      nameTipsArray: [], // 
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData: [], // 
      customer_info_list: [], // 
      rules: {
        customer_info: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },

      multipleSelection: [], // 
      solidSelection: [], // 
      solidSelection: [], // 
    };
  },
  methods: {
    listenShowFill(value){
		if(value=="999999999"){
			this.isShowFill = 'inherit';
		};
	},
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    handleClose() {
      this.addDV = false;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {
      console.log(val);
      if (val.length == 0) {
        this.isNoClick = true;
      } else {
        this.isNoClick = false;
      }
      this.multipleSelection = val;
    },
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },

    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([
          {
            value: "",
          },
        ]);
      }
    },

    // 
    formSearch() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "/api/query/getSingleProductLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
          customer_id: this.customer_id,
        },
      })
        .then((res) => {
          console.log(res);
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            console.log(res);
            this.queryResData = [];
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;
              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");

              item.leave_count =
                Number(item.storage_count) - Number(item.out_count);
              item.count = "";
              this.queryResData.push(item);
            }
            console.log(this.queryResData);
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    refreshFormSearch() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "/api/query/getProductLocateByTime",
        data: {
          customer_info: this.customer_id,
        },
      })
        .then((res) => {
          console.log(res);
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            console.log(res);
            this.queryResData = [];
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let today = Date.parse(new Date(this.today_date));
              var day = parseInt((today - come) / (1000 * 60 * 60 * 24)); //
              item.save_days = (isNaN(day)||day<0 )? 0 :day;
              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");

              item.leave_count =
                Number(item.storage_count) - Number(item.out_count);
              item.count = "";
              this.queryResData.push(item);
            }
            console.log(this.queryResData);
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    addOpen() {
      this.solidSelection = this.multipleSelection;
      for (let item of this.solidSelection) {
        if (item.count == "") {
          this.$message.warning("'',");
          return;
        }

        if (item.count < 0) {
          this.$message.warning("!");
          return;
        }

        if (item.count > item.leave_count) {
          this.$message.warning("!");
          return;
        }
        if (isNaN(Number(item.count))) {
          this.$message.warning("!");
          return;
        }
      }
      this.addDV = true;
    },

    // 
    addToSubmit() {
      if (
        this.addForm.customer_address == "" ||
        this.addForm.street_address1 == "" ||
        this.addForm.street_address2 == "" ||
        this.addForm.zip == "" ||
        this.addForm.city == "" ||
        this.addForm.province == ""
      ) {
        this.$message.warning(",");
        return;
      }

      while (this.solidSelection.length != 0) {
        let random = Math.random().toString(16).slice(2,15);
        this.solidSelection[0].out_time = getNowFormatDate();
        this.updateProduct(this.solidSelection[0], random);
        this.insertOutRecord(this.solidSelection[0], random);
        this.insertAddress(random, this.addForm);

        this.solidSelection.shift();
      }
      this.refreshFormSearch();
      this.addDV = false;
    },

    //   
    insertOutRecord(data, random) {
      this.$http({
        method: "post",
        url: "api/insert/insertOutRecord",
        data: {
          customer_id: data.customer_id,
          product_name: data.product_name,
          product_sku: data.product_sku,
          out_time: data.out_time,
          out_count: data.count,
          record_code: random,
          status: "false",
          out_source: "",
        },
      })
        .then((res) => {
          console.log("");
        })
        .catch((err) => {});
    },

    //   
    insertAddress(random, data) {
      data.record_code = random;
      data.product_code = this.solidSelection[0].product_code;
      console.log(data);
      this.$http({
        method: "post",
        url: "api/insert/insertAddress",
        data: data,
      })
        .then((res) => {
          console.log("");
          this.$message.success("");
        })
        .catch((err) => {});
    },

    //  
    updateProduct(data, random) {
      data.record_code = random;
      data.out_count = String(Number(data.out_count) + Number(data.count));
      this.$http({
        method: "post",
        url: "api/update/updateProduct",
        data: data,
      })
        .then((res) => {
          this.refreshGetAllProduct();
        })
        .catch((err) => {});
    },

    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getSingleProductLocate",
        data: {
          customer_info: this.formData.customer_info,
          customer_id: this.customer_id,
        },
      })
        .then((res) => {
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item

              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    if (prop == "company_name") {
                      continue;
                    }
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
  },
  computed: {
    computedQueryResData() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData.slice(size * (current - 1), size * current);
    },
  },
  mounted() {
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;

    this.refreshFormSearch();
  },
  created() {
    this.customer_id = sessionStorage.getItem("userName");
    this.level = sessionStorage.getItem("userLevel");
  },
};
</script>
<style lang="less" scoped>
.customer_id_style {
  margin: 0;
  font-size: 20px;
}
.cus-dialog-style {
  /deep/ .el-dialog {
    margin-top: 1vh !important;
  }
  .el-input {
    width: 300px !important;
  }
}
</style>
