<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>

    <div class="content-container">
      <el-row>
        <el-col :span="10">
          <el-form
            :model="formData"
            ref="formData"
            label-width="100px"
            label-position="left"
          >
            <el-form-item label=":" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=""
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:10px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col class="customer-table" :span="24">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <!-- <el-table-column align="center" type="selection" width="100px"></el-table-column> -->
            <el-table-column
              align="center"
              label=""
              prop="customer_id"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="company_name"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="product_name"
              width="200px"
            ></el-table-column>
            <el-table-column
              align="center"
              label="(cm)"
              prop="length_width_height"
              width="150px"
            ></el-table-column>
            <el-table-column
              align="center"
              label="(lb)"
              prop="weight"
              width="130"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="come_time"
              width="130"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="out_time"
              width="130"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="save_days"
              width="130"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="count"
              width="130"
            ></el-table-column>
            <el-table-column align="center" label="" width="200px">
              <template slot-scope="scope">
                <div class="sa-container">
                  <el-input
                    placeholder=""
                    v-model="scope.row.operateCount"
                  ></el-input>
                </div>
                <el-button
                  style="margin-left:5px"
                  class="modify-btn small-btn"
                  @click="outSubmit(scope.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="24">
          <el-pagination
            class="page-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { utcToCst } from "../../utils/utcToCst";
import { getNowFormatDate } from "../../utils/getCurrentTime";
export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      user: "", // 
      level: "", // 
      times: 0, // 
      timer: null,
      loading: false, // false,true
      today_date: "", // 
      pickerOption: {
        disabledDate: (time) => {
          return time.getTime() > Date.now(); /**/
        },
      },
      addDV: false, // 
      boxReqUrl: "", // 
      boxReqData: {}, // 
      locateReqUrl: "/api/query/getLocateCustomerId", // 
      locateReqData: {}, // 
      nameTipsArray: [], // 
      formData: {
        // 
        customer_info: "", // ,
      },
      queryResData: [], // 
      customer_info_list: [], // 
      louqueForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        length_width_height: "(cm)",
        weight: "(lb)",
        count: "",
        save_days: "",
        out_count: "",
        box_code: "",
        status: "",
      },
      addForm: {
        customer_id: "",
        come_time: "",
        product_name: "",
        length_width_height: "",
        weight: "",
        count: "",
        save_days: "",
        out_count: "",
        box_code: "",
        status: "",
      },
      rules: {
        customer_info: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      addRules: {
        customer_id: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        come_time: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        product_name: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        length_width_height: [
          {
            message: "(cm),'30*40*50'",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        weight: [
          {
            message: "(lb)",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        count: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        save_days: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        out_count: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
        status: [
          {
            message: "",
            required: true,
            trigger: ["blur", "change"],
          },
        ],
      },
      queryPage: {
        // 
        pageSize: 10,
        currentPage: 1,
      },
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    handleSelectionChange(val) {},
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
    // 
    formSearch() {
      this.getBox();
    },

    // 
    getBox() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getBoxLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            this.$message.success("");
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));

              let out = Date.parse(new Date(item.out_time));
              if (item.out_count == 0) {
                item.save_days = 0;
                item.out_time = "";
              } else {
                var day = parseInt((out - come) / (1000 * 60 * 60 * 24)); //
                item.save_days = (isNaN(day)||day<0 )? 0 :day;
                item.out_time = utcToCst(item.out_time)
                  .slice(0, 10)
                  .replace(/|||||||/g, "");
              }

              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
              item.operateCount = "";
                item.leave_count = Number(item.count) 
                 item.count = Number(item.count)+Number(item.out_count)
            }
            this.queryResData = res.data;
          } else {
            this.$message.warning("");
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    refreshGetBox() {
      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getBoxLocateByTime",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false;
          this.queryResData = [];
          if (res.data.length != 0) {
            for (let item of res.data) {
              let come = Date.parse(new Date(item.come_time));
              let out = Date.parse(new Date(item.out_time));
              if (item.out_count == 0) {
                item.save_days = 0;
                item.out_time = "";
              } else {
                var day = parseInt((out - come) / (1000 * 60 * 60 * 24)); //
                item.save_days = (isNaN(day)||day<0 )? 0 :day;
                item.out_time = utcToCst(item.out_time)
                  .slice(0, 10)
                  .replace(/|||||||/g, "");
              }
              item.come_time = utcToCst(item.come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
 item.leave_count = Number(item.count) 
                 item.count = Number(item.count)+Number(item.out_count)
              item.operateCount = "";
            }
            this.queryResData = res.data;
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },
    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getBoxLocate",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
    // ()
    addOpen() {
      if (this.formData.customer_info != "") {
        this.addForm.customer_id = this.formData.customer_info;
      }
      this.addDV = true;
    },
    // 
    outSubtract(row) {
      if (row.operateCount == 0) {
        this.$message.warning("0");
        return;
      }
      row.count++;
      row.out_count--;
      row.operateCount--;
    },
    // 
    outAdd(row) {
      if (row.count == 0) {
        this.$message.warning("0!");
        return;
      }
      row.count--;
      row.out_count++;
      row.operateCount++;
    },
    // 
    outSubmit(row) {
      if (row.operateCount == "") {
        this.$message.warning("!");
        return;
      }

      // count  +>
      if (
        Number(row.out_count) + Number(row.operateCount) >
        Number(row.count)
      ) {
        this.$message.warning("");
        return;
      } else if (Number(row.count) < 0) {
        this.$message.warning("");
        return;
      } else if (isNaN(Number(row.count))) {
        this.$message.warning("!");
        return;
      }
      this.$confirm("?")
        .then(() => {
          row.count = String(Number(row.count) - Number(row.operateCount));
          row.out_count = String(
            Number(row.out_count) + Number(row.operateCount)
          );
          let data = {};
          let record_code = Math.random().toString(16).slice(2,15);
          for (let name in row) {
            if (name == "operateCount") {
              continue;
            }
            data[name] = row[name];
          }
          data.out_time = getNowFormatDate();

          this.updateBox(data);
          this.insertOutRecord(data, row, record_code);
        })
        .catch((err) => {
          console.log(err);
          this.$message.info("");
        });
    },

    // 
    updateBox(data) {
      this.$http({
        method: "post",
        url: "api/update/updateBox",
        data: data,
      })
        .then((res) => {
          this.refreshGetBox();
          this.$message.success("");
        })
        .catch((err) => {});
    },

    //   
    insertOutRecord(data, row, random) {
      this.$http({
        method: "post",
        url: "api/insert/insertOutRecord",
        data: {
          customer_id: data.customer_id,
          product_name: data.product_name,
          product_sku: "---",
          out_time: data.out_time,
          out_count: row.operateCount,
          record_code: random,
          status: "false",
          out_source: "",
        },
      })
        .then((res) => {
          console.log("");
        })
        .catch((err) => {});
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
    addForm: {
      handler: function(nV, oV) {
        // nV.save_days = nV.come_time;

        // 
        if (this.addForm.come_time != "") {
          var a1 = Date.parse(new Date(nV.come_time));
          var a2 = Date.parse(new Date(nV.out_time));
          var day = parseInt((a2 - a1) / (1000 * 60 * 60 * 24)); //
          nV.save_days = (isNaN(day)||day<0 )? 0 :day + 1;
        }

        // 
        if (this.addForm.length_width_height.length > 4) {
          let lwhArr = this.addForm.length_width_height.split("*");
          if (lwhArr.length == 3) {
            let result = Math.floor(
              (lwhArr[0] * lwhArr[1] * lwhArr[2]) / 6000 / 0.452
            );
            this.addForm.weight = result;
          }
        }
      },
      deep: true,
    },
  },
  computed: {
    computedQueryResData() {
      let size = this.queryPage.pageSize;
      let current = this.queryPage.currentPage;
      return this.queryResData.slice(size * (current - 1), size * current);
    },
  },
  mounted() {
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;

    this.refreshGetBox();
  },
  created() {
    this.user = sessionStorage.getItem("userName");
    this.level = sessionStorage.getItem("userLevel");
    let that = this;
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/");
    }
  },
};
</script>

<style lang="less" scoped>
.productPage-addForm {
  margin-top: -95px;
  /deep/ .el-form-item__error {
    padding-left: 20px;
  }
}
.sa-container {
  display: inline-block;
  margin-left: -15px;
  max-width: 80px;
}
</style>
