<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>
    <div class="content-container">
      <el-row>
        <el-col style="width:31%">
          <el-form
            :model="formData"
            ref="formData"
            label-width="120px"
            label-position="left"
          >
            <el-form-item label="" prop="customer_info">
              <el-autocomplete
                style="width:100%"
                v-model="formData.customer_info"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
                placeholder=""
              ></el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="margin-left:30px;" :span="2">
          <el-button type="primary" @click="formSearch"></el-button>
        </el-col>
        <el-col :span="3">
          <el-button type="info" @click="resetForm('formData')"></el-button>
        </el-col>

        <el-col :span="2">
          <div style="padding-top:5px"></div>
        </el-col>
        <el-col :span="5">
          <el-date-picker
            :picker-options="pickerOption"
            type="date"
            placeholder=""
            value-format="yyyy-M-d"
            v-model="choose_time"
          ></el-date-picker>
        </el-col>
      </el-row>

      <el-row style="padding-top:5px">
        <el-col class="customer-table" :span="20">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedQueryResData"
            ref="multipleTable"
          >
            <el-table-column
              align="center"
              label=""
              prop="customer_id"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="company_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="trading_date"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="trading_content"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="trading_amount"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="20">
          <el-pagination
            class="customer-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";
export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      loading: false, // false,true
      queryPage: {
        pageSize: 10,
        currentPage: 1,
      },
      formData: {
        // 
        customer_info: "", // ,
      },
      choose_time: "",
      pickerOption: {
        disabledDate: (time) => {
          return time.getTime() > Date.now(); /**/
        },
      },
      queryResData: [], // 
      locateReqUrl: "/api/query/getLocateCustomerId", // 
      locateReqData: {}, // 
      nameTipsArray: [], // 
      addForm: {
        customer_id: "", // 
        company_name: "", // 
        first_come_time: "", // 
      }, // 
      multipleSelection: [], // 
      solidSelection: [], // 
      addRules: {},
    };
  },
  methods: {
    handleClose() {
      this.addDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      if (val.length == 0) {
        this.isDel = true;
      } else {
        this.isDel = false;
      }
      this.multipleSelection = val;
    },

    // 
    querySearch(queruString, cb) {
      if (this.formData.customer_info != "") {
        var nameTipsArray = this.nameTipsArray;
        cb(nameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },
    // 
    getData() {
      this.$http({
        method: "post",
        url: "api/query/getTradingLocate",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.customer_info_list = res.data;
          if (this.formData.customer_info != "") {
            this.nameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_info_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.formData.customer_info) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.nameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
    // 
    formSearch() {
      if (this.choose_time == null) {
        this.choose_time = "";
      }

      this.loading = true;

      // if (this.choose_time != "" || this.choose_time != null) {
      this.$http({
        method: "post",
        url: "api/query/getTradingByName",
        data: {
          customer_info: this.formData.customer_info,
        },
      })
        .then((res) => {
          this.loading = false; this.queryResData=[];
          if (res.data.length == 0 && this.choose_time != "") {
            this.$message.warning(`${this.choose_time}`);
            return;
          } else if (res.data.length == 0 && this.choose_time == "") {
            this.$message.warning("");
            return;
          }

          if (res.data.length != 0) {
            console.log("");
            this.$message.success("");
            for (let item of res.data) {
              item.trading_date = utcToCst(item.trading_date)
                .slice(0, 10)
                .replace(/|||||||/g, "")
                .replace(/\//g, "-");
            }

            if (this.choose_time != "") {
              this.queryResData = [];

              // 
              for (let item of res.data) {
                if (item.trading_date == this.choose_time) {
                  this.queryResData.push(item);
                }
              }
              //  
              if (this.queryResData.length == 0) {
                if (this.choose_time != "" && this.choose_time != null) {
                  this.$message.warning(
                    `${this.choose_time}`
                  );
                }
                return;
              }
            } else {
              // 
              this.queryResData = res.data;
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
      //  }
      //   else {
      //   this.refreshFormSearch();
      // }
    },
    // 
    refreshFormSearch() {
      if (this.choose_time == null) {
        this.choose_time = "";
      }

      this.loading = true;
      this.$http({
        method: "post",
        url: "api/query/getTradingByName",
        data:{
          customer_info:this.formData.customer_info
        },
      })
        .then((res) => {
          console.log(res);
          this.loading = false; this.queryResData=[];
          if (
            res.data.length == 0 &&
            (this.choose_time != "" || this.choose_time != null)
          ) {
            this.$message.warning(`${this.choose_time}`);
            return;
          } else if (
            res.data.length == 0 &&
            (this.choose_time == "" || this.choose_time == null)
          ) {
            this.$message.warning("");
            return;
          }
          this.loading = false; this.queryResData=[];
          if (res.data.length != 0) {
            for (let item of res.data) {
              item.trading_date = utcToCst(item.trading_date)
                .slice(0, 10)
                .replace(/|||||||/g, "")
                .replace(/\//g, "-");
            }
            // this.queryResData = res.data;
            if (this.choose_time != "") {
              this.queryResData = [];

              // 
              for (let item of res.data) {
                if (item.trading_date == this.choose_time) {
                  this.queryResData.push(item);
                }
              }
              //  
              if (this.queryResData.length == 0) {
                if (this.choose_time != "" && this.choose_time != null) {
                  this.$message.warning(
                    `${this.choose_time}`
                  );
                }
                return;
              }
            } else {
              // 
              console.log("");
              this.queryResData = res.data;
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    resetForm(formName) {
      let form = this.$refs[formName];
      form.resetFields();
    },
  },
  watch: {
    formData: {
      handler: function(nV, oV) {
        this.debounce(this.getData, 1000);
      },
      deep: true,
    },
    choose_time(nV, oV) {
      if (nV != "") {
        this.refreshFormSearch();
      }
    },
  },
  mounted() {
    this.refreshFormSearch();
  },
  computed: {
    computedQueryResData() {
      return this.queryResData.slice(
        (this.queryPage.currentPage - 1) * this.queryPage.pageSize,
        this.queryPage.currentPage * this.queryPage.pageSize
      );
    },
  },
  created() {
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/");
    }
  },
};
</script>

<style lang="less"></style>
