<template>
  <div class="header-container">
    <div class="module-title">
      <h1></h1>
    </div>
    <div class="content-container">
      <el-row>
        <el-col class="customer-table" :span="20">
          <el-table
            :height="tableHeight"
            v-loading="loading"
            element-loading-text="..."
            element-loading-custom-class="loading_color"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.5)"
            @selection-change="handleSelectionChange"
            :row-style="showRow"
            border
            stripe
            :data="computedqueryResData"
            ref="multipleTable"
          >
            <el-table-column
              align="center"
              type="selection"
              width="100px"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="customer_id"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="company_name"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="first_come_time"
            ></el-table-column>
            <el-table-column
              align="center"
              label=""
              prop="auto_pwd"
            ></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb">
        <el-col :span="20">
          <el-pagination
            class="customer-pagination"
            :background="true"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryPage.currentPage"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryPage.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryResData.length"
          >
          </el-pagination>
        </el-col>
      </el-row>
      <el-row type="flex" class="padding_tb padding_lr">
        <el-col :span="2">
          <el-button class="add-btn" @click="addDialogOpen"></el-button>
        </el-col>
        <el-col :span="14">
          <el-button
            type="warning"
            :disabled="isForbiddenMod"
            @click="modifyDialogOpen"
            ></el-button
          >
        </el-col>
        <el-col :span="2">
          <el-button
            type="danger"
            :disabled="isForbiddenDel"
            @click="deleteSubmit"
            ></el-button
          >
        </el-col>
        <el-col :span="2">
          <el-button type="info" plain @click="toggleSelection()"
            ></el-button
          >
        </el-col>
      </el-row>
    </div>
    <el-dialog
      :close-on-click-modal="false"
      title="--"
      class="page-dialog"
      :visible.sync="addDV"
      width="35%"
      :before-close="handleCloseAdd"
    >
      <el-form
        v-model="addForm"
        ref="addForm"
        :rules="addRules"
        label-width="120px"
      >
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 10px; margin-top: 25px"
        >
          <el-form-item label=":">
            <el-col :span="24" style="padding-left: 20px">
              <el-autocomplete
                style="width: 100%"
                v-model="addForm.customer_id"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 25px; margin-top: 25px"
        >
          <el-form-item label=":">
            <el-col :span="24" style="padding-left: 20px">
              <el-autocomplete
                style="width: 100%"
                v-model="addForm.company_name"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 10px; margin-top: 15px"
        >
          <el-form-item label=":" labelWidth="150px">
            <el-col :span="24" style="padding-left: 20px">
              <el-date-picker
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
                v-model="addForm.first_come_time"
              ></el-date-picker>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addSubmit"> </el-button>
        <el-button type="primary" plain @click="addDV = false"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      :close-on-click-modal="false"
      title="--"
      class="page-dialog"
      :visible.sync="modifyDV"
      width="35%"
      :before-close="handleCloseModify"
    >
      <el-form v-model="modifyForm" ref="modifyForm" label-width="120px">
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 10px; margin-top: 25px"
        >
          <el-form-item label=":">
            <el-col :span="24" style="padding-left: 20px">
              <el-autocomplete
                style="width: 100%"
                v-model="modifyForm.customer_id"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 25px; margin-top: 25px"
        >
          <el-form-item label=":">
            <el-col :span="24" style="padding-left: 20px">
              <el-autocomplete
                style="width: 100%"
                v-model="modifyForm.company_name"
                :fetch-suggestions="querySearch"
                clearable
                id="formSearch"
              ></el-autocomplete>
            </el-col>
          </el-form-item>
        </el-row>
        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 10px; margin-top: 15px"
        >
          <el-form-item label=":" labelWidth="150px">
            <el-col :span="24" style="padding-left: 20px">
              <el-date-picker
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
                v-model="modifyForm.first_come_time"
              ></el-date-picker>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 25px; margin-top: 25px"
        >
          <el-form-item label=":">
            <el-col :span="24" style="padding-left: 20px">
              <el-input v-model="modifyForm.leave_amount"></el-input>
            </el-col>
          </el-form-item>
        </el-row>

        <el-row
          type="flex"
          justify="center"
          style="margin-bottom: 25px; margin-top: 25px"
        >
          <el-form-item label=":">
            <el-col :span="24" style="padding-left: 20px">
              <el-input v-model="modifyForm.auto_pwd"></el-input>
            </el-col>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="modifySubmit"> </el-button>
        <el-button type="primary" plain @click="modifyDV = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { utcToCst } from "@/utils/utcToCst";
import { getNowFormatDate } from "@/utils/getCurrentTime";

export default {
  data() {
    return {
      tableHeight: window.innerHeight * 0.65,
      addDV: false, // 
      modifyDV: false, // 
      isForbiddenDel: true, // 
      isForbiddenMod: true, // 
      loading: false, // 
      customer_id_list: [], //  
      addNameTipsArray: [], //  
      queryPage: {
        pageSize: 10,
        currentPage: 1,
      },
      queryResData: [], // 
      addForm: {
        // 
        customer_id: "", // 
        company_name: "", // 
        first_come_time: "", // 
      },
      modifyForm: {
        // 
        customer_id: "", // 
        company_name: "", // 
        first_come_time: "", // 
        leave_amount: "", // 
        auto_pwd: "", // 
      },
      multipleSelection: [], // 
      solidSelection: [], // 
      addRules: {},
    };
  },
  methods: {
    handleCloseAdd() {
      this.addDV = false;
    },
    handleCloseModify() {
      this.modifyDV = false;
    },
    handleSizeChange(val) {
      this.queryPage.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryPage.currentPage = val;
    },
    // 
    showRow({ row, rowIndwx }) {
      let styleJson = {
        "background-color": "#E4F9E5",
        "font-weight": "400",
        "font-size": "15px",
        color: "#000000",
      };

      return styleJson; // 
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      if (val.length == 0) {
        this.isForbiddenDel = true;
        this.isForbiddenMod = true;
      } else if (val.length == 1) {
        this.isForbiddenMod = false;
        this.isForbiddenDel = false;
      } else {
        this.isForbiddenDel = false;
        this.isForbiddenMod = true;
      }
      this.multipleSelection = val;
    },
    // 
    refreshGetCustomer() {
      this.loading = true;
      this.$http({
        method: "get",
        url: "api/query/getCustomer",
      }).then((res) => {
        this.loading = false;
        this.queryResData = [];
        if (res.data.length != 0) {
          for (let item of res.data) {
            item.first_come_time = utcToCst(item.first_come_time)
              .slice(0, 10)
              .replace(/|||||||/g, "");
          }
          this.queryResData = res.data;
        } else {
          this.$message.warning("");
        }
      });
    },

    // 
    modifyDialogOpen() {
      this.$http({
        method: "post",
        url: "api/query/getSpecificCustomer",
        data: {
          customer_id: this.multipleSelection[0].customer_id, // 
        },
      })
        .then((res) => {
          if (res.data.length == 0) {
            this.$message.error("");
            return;
          } else {
            for (let item of res.data) {
              item.first_come_time = utcToCst(item.first_come_time)
                .slice(0, 10)
                .replace(/|||||||/g, "");
            }

            console.log(res.data[0]);
            this.modifyForm = res.data[0];
            this.modifyDV = true;
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    querySearch(queruString, cb) {
      if (this.addForm.customer_id != "" || this.addForm.company_name != "") {
        var addNameTipsArray = this.addNameTipsArray;

        cb(addNameTipsArray);
      } else {
        cb([{ value: "" }]);
      }
    },

    // 
    modifySubmit() {
      this.checkCustomerId("modify");
    },

    // 
    addDialogOpen() {
      this.addDV = true;
    },
    // 
    addSubmit() {
      this.checkCustomerId("add");
    },

    // id
    checkCustomerId(exc) {
      this.$http({
        method: "post",
        url: "api/query/getCustomerId",
        data: {
          customer_id: this.addForm.customer_id,
        },
      })
        .then((res) => {
          if (res.data.length == 0) {
            if (exc == "add") {
              this.insertCustomer();
            } else if (exc == "modify") {
              this.modifyCustomer();
            }
          } else {
            this.$message.warning("");
            return;
          }
        })
        .catch((err) => {});
    },

    // 
    insertCustomer() {
      this.$http({
        method: "post",
        url: "api/insert/insertCustomer",
        data: {
          customer_id: this.addForm.customer_id, // 
          company_name: this.addForm.company_name, // 
          first_come_time: this.addForm.first_come_time, // 
          leave_amount: "0", // 
          auto_pwd: Math.random().toString(16).slice(2,8), // 
        },
      })
        .then((res) => {
          this.$message.success("!");
          this.addDV = false;
          this.refreshGetCustomer();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    modifyCustomer() {
      this.$http({
        method: "post",
        url: "api/update/updateSpecificCustomer",
        data: {
          customer_id: this.modifyForm.customer_id, // 
          company_name: this.modifyForm.company_name, // 
          first_come_time: this.modifyForm.first_come_time, // 
          leave_amount: this.modifyForm.leave_amount, // 
          auto_pwd: this.modifyForm.auto_pwd, // 
        },
      })
        .then((res) => {
          this.$message.success("!");
          this.modifyDV = false;
          this.refreshGetCustomer();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    deleteCustomer() {
      this.$http({
        method: "post",
        url: "api/delete/deleteCustomer",
        data: {
          customer_id: this.solidSelection[0].customer_id,
        },
      })
        .then((res) => {
          this.refreshGetCustomer();
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
    // 
    deleteSubmit() {
      this.$confirm("?")
        .then(() => {
          this.solidSelection = this.multipleSelection;
          while (this.solidSelection.length != 0) {
            this.deleteCustomer();
            this.solidSelection.shift();
          }
          this.$message.success("!");
        })
        .catch((err) => {
          this.$message.warning("");
        });
    },

    // 
    getDialogData() {
      let prop;
      if (this.modifyDV == true) {
        prop = this.modifyForm.customer_id;
      } else if (this.addDV == true) {
        prop = this.addForm.customer_id;
      }
      this.$http({
        method: "post",
        url: "api/query/getCustomerDialogLocate",
        data: {
          customer_info: prop,
        },
      })
        .then((res) => {
          this.customer_info_list = [];
          this.customer_id_list = res.data;
          if (this.addForm.customer_id != "") {
            this.addNameTipsArray = [];
            let avoidSameArr = [];
            // ,,
            // ,,
            for (let item of this.customer_id_list) {
              let flag = 0; // 
              // item
              for (let prop in item) {
                if (
                  String(item[prop]).indexOf(this.addForm.customer_id) != -1
                ) {
                  // ,,
                  for (let val of avoidSameArr) {
                    if (val == item[prop]) {
                      flag = 1;
                      break;
                    }
                  }
                  if (flag == 0) {
                    this.addNameTipsArray.push({
                      value: String(item[prop]),
                    });
                    avoidSameArr.push(String(item[prop]));
                  } else {
                    continue;
                  }
                }
              }
            }
          }
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },

    // 
    debounce(fn, wait) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, wait);
    },
  },
  mounted() {
    var today = new Date();
    today.setTime(today.getTime());
    var today_date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    this.today_date = today_date;
    this.refreshGetCustomer();
  },
  computed: {
    computedqueryResData() {
      return this.queryResData.slice(
        (this.queryPage.currentPage - 1) * this.queryPage.pageSize,
        this.queryPage.currentPage * this.queryPage.pageSize
      );
    },
  },
  created() {
    if (sessionStorage.getItem("userLevel") == "") {
    } else {
      this.$message.warning("!");
      this.$router.push("/home");
    }
  },

  watch: {
    addForm: {
      handler: function (nV, oV) {
        this.debounce(this.getDialogData, 1000);
      },
      deep: true,
    },
    modifyForm: {
      handler: function (nV, oV) {
        this.debounce(this.getDialogData, 1000);
      },
      deep: true,
    },
  },
};
</script>

<style lang="less"></style>
